"use strict";
const Game_1 = require("./Game");
const Config_1 = require("./Config");
const index_1 = require("./PlayerInterface/index");
class HLV {
    constructor(game) {
        this.game = game;
    }
    load(name) {
        this.game.load(name);
    }
    setTitle(title) {
        this.game.setTitle(title);
    }
    getTitle() {
        return this.game.getTitle();
    }
}
HLV.VERSION = VERSION;
var HLViewer;
(function (HLViewer) {
    function init(rootSelector, params) {
        const node = document.querySelector(rootSelector);
        if (!node) {
            return null;
        }
        const config = Config_1.Config.init(params);
        const result = Game_1.Game.init(config);
        if (result.status === 'success') {
            const game = result.game;
            const ui = new index_1.PlayerInterface(game, node);
            ui.draw();
            game.draw();
            return new HLV(game);
        }
        return null;
    }
    HLViewer.init = init;
})(HLViewer || (HLViewer = {}));
module.exports = HLViewer;
